import React, { createElement } from 'react'
import { Link, Button, Label, Head, SubHead, P } from '../components'
import rehypeReact from 'rehype-react'

const renderAst = new rehypeReact({
  createElement,
  components: {
    p: P,
    h3: SubHead,
    link: Link
  }
}).Compiler

export default ({ data }) => {
  if(!data || !data.markdownRemark) return false
  const { markdownRemark: { htmlAst, frontmatter } } = data
  return (
    <div>
      { frontmatter.subtitle && <Label>{ frontmatter.subtitle }</Label> }
      { frontmatter.title && <Head>{ frontmatter.title }</Head> }
      { renderAst(htmlAst) }
    </div>
  )
}
export const query = graphql`
  query PostQuery($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: {slug: {eq: $slug}}) {
      excerpt
      htmlAst
      frontmatter {
        title
        subtitle
      }
    }
  }
`



